/* @flow */

import _ from 'lodash';
import test from 'Src/test';

if (process.env.NODE_ENV === 'production') {
  require('Src/prod');
} else {
  require('Src/dev');
}

test();

function add(num1: number, num2: number) {
  return num1 + num2;
}

export default function () {
  const x: number = add(3, 0);

  return x;
}



